.collection-page{
    display:flex;
    flex-direction: column;

    .title{
        font-size:30px;
        margin:0 auto 30px;
    }

    .items{
        display:grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 10px;

        &.collection-item{
            margin-bottom:30px;
        }
    }
}